
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: rgb(73, 79, 82);
  color: white;
}

.nav-links {
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  height: 5rem;
}
nav > *, .nav-links > * {
  color: white;
}

.nav-links > * {
  transition: all 0.1s ease-in-out;
}

.cart {
  border: 1px solid white;
  padding: 1rem;
  border-radius: 50px;
}

.homepageDescription {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  height: 60%;
  row-gap: 5rem;

}
.homepageDescription > button {
  height: 3rem;
  width: 10rem;
}

.homepageImage > img {
  height: 30rem;
  width: 25rem;
  border-radius: 10px;
}


.homepage {
display: grid;
grid-template-columns: 1fr 1fr;
width: 80%;
padding-top: 3rem;
padding-right: 2rem;
padding-left: 10rem;
}

a {
  text-decoration: none;
}

.nav-links > *:hover {
  transform: scale(1.1);
}

.product {
  list-style-type: none;
  border: 0.5px solid rgb(112, 109, 109);
  border-radius: 10px;
  height: 35rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
}


.productList {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: 3rem;
  column-gap: 2rem;
  padding-right: 2rem;
}
.product > img {
  height: 19rem;
  width: 18rem;
}

.product > * {
  padding: 0.5rem;
}
.product > button {
  border: none;
  background-color: black;
  color: white;
  width: 15rem;
}

.product > button:hover {
  cursor: pointer;
  opacity: 0.7;
}

.homepageButton {
  border: none;
  color: white;
  background-color: black;
  border-radius: 35px;
  width: 18rem;
  height: 4rem;
  font-size: 2rem;
  transition: all 0.1s ease-in-out;
}

.homepageButton:hover {
  cursor: pointer;
  transform: scale(1.1);
}

body {
background-image: url("./images/backgroundImage.jpg");
background-size: cover;
}